export const validateRegistrationForm = (values) => {
    const errors = {
        username: undefined,
        password: undefined,
        email: undefined,
        birth_date: undefined,
        sex: undefined,
    };

    if (!values.username) {
        errors.username = '   ';
    }

    if (!values.password) {
        errors.password = '  ';
    }

    if (!values.email) {
        errors.email = '  email';
    }

    if (!values.first_name) {
        errors.birth_date = '   ';
    }

    if (!values.last_name) {
        errors.sex = '   ';
    }

    return errors;
};
